/**
 * We are defining all the global rules here and configuring
 * all the `vee-validate` settings.
 */
import { configure, defineRule, Field, Form, ErrorMessage } from "vee-validate";
import { localize, setLocale } from "@vee-validate/i18n";
import ar from "@vee-validate/i18n/dist/locale/ar.json";
import bn from "@vee-validate/i18n/dist/locale/bn.json";
import de from "@vee-validate/i18n/dist/locale/de.json";
import en from "@vee-validate/i18n/dist/locale/en.json";
import es from "@vee-validate/i18n/dist/locale/es.json";
import fa from "@vee-validate/i18n/dist/locale/fa.json";
import fr from "@vee-validate/i18n/dist/locale/fr.json";
import he from "@vee-validate/i18n/dist/locale/he.json";
import hi_IN from "../../locales/hi_IN.json";
import it from "@vee-validate/i18n/dist/locale/it.json";
import ja from "@vee-validate/i18n/dist/locale/ja.json";
import nl from "@vee-validate/i18n/dist/locale/nl.json";
import pl from "@vee-validate/i18n/dist/locale/pl.json";
import pt_BR from "@vee-validate/i18n/dist/locale/pt_BR.json";
import ru from "@vee-validate/i18n/dist/locale/ru.json";
import sin from "../../locales/sin.json";
import tr from "@vee-validate/i18n/dist/locale/tr.json";
import uk from "@vee-validate/i18n/dist/locale/uk.json";
import zh_CN from "@vee-validate/i18n/dist/locale/zh_CN.json";
import { all } from "@vee-validate/rules";

window.defineRule = defineRule;

export default {
    install: (app) => {
        /**
         * Global components registration;
         */
        app.component("VForm", Form);
        app.component("VField", Field);
        app.component("VErrorMessage", ErrorMessage);

        window.addEventListener("load", () => setLocale(document.documentElement.attributes.lang.value));

        /**
         * Registration of all global validators.
         */
        Object.entries(all).forEach(([name, rule]) => defineRule(name, rule));

        /**
         * This regular expression allows phone numbers with the following conditions:
         * - The phone number can start with an optional "+" sign.
         * - After the "+" sign, there should be one or more digits.
         *
         * This validation is sufficient for global-level phone number validation. If
         * someone wants to customize it, they can override this rule.
         */
        defineRule("phone", (value) => {
            if (!value || !value.length) {
                return true;
            }

            if (!/^\+?\d+$/.test(value)) {
                return false;
            }

            return true;
        });

        defineRule("address", (value) => {
            if (!value || !value.length) {
                return true;
            }

            if (
                !/^[a-zA-Z0-9\s.\/*'\u0600-\u06FF\u0750-\u077F\u08A0-\u08FF\u0590-\u05FF\u3040-\u309F\u30A0-\u30FF\u0400-\u04FF\u0D80-\u0DFF\u3400-\u4DBF\u2000-\u2A6D\u00C0-\u017F\u0980-\u09FF\u0900-\u097F\u4E00-\u9FFF,\(\)-]{1,60}$/iu.test(
                    value
                )
            ) {
                return false;
            }

            return true;
        });

        defineRule(
            "decimal",
            (value, { decimals = "*", separator = "." } = {}) => {
                if (value === null || value === undefined || value === "") {
                    return true;
                }

                if (Number(decimals) === 0) {
                    return /^-?\d*$/.test(value);
                }

                const regexPart = decimals === "*" ? "+" : `{1,${decimals}}`;
                const regex = new RegExp(
                    `^[-+]?\\d*(\\${separator}\\d${regexPart})?([eE]{1}[-]?\\d+)?$`
                );

                return regex.test(value);
            }
        );

        configure({
            /**
             * Built-in error messages and custom error messages are available. Multiple
             * locales can be added in the same way.
             */
            generateMessage: localize({
                en: {
                    ...en,
                    messages: {
                        ...en.messages,
                        phone: "This {field} must be a valid phone number",
                        address: "This {field} must be a valid address",
                    },
                },

                ar: {
                    ...ar,
                    messages: {
                        ...ar.messages,
                        phone: "    {field}   ",
                        address: "    {field}  ",
                    },
                },

                bn: {
                    ...bn,
                    messages: {
                        ...bn.messages,
                        phone: " {field}      ",
                        address: " {field}     ",
                    },
                },

                de: {
                    ...de,
                    messages: {
                        ...de.messages,
                        phone: "Dieses {field} muss eine gltige Telefonnummer sein",
                        address: "Diese {field} muss eine gltige Adresse sein",
                    },
                },

                es: {
                    ...es,
                    messages: {
                        ...es.messages,
                        phone: "Este {field} debe ser un nmero de telfono vlido",
                        address: "Esta {field} debe ser una direccin vlida",
                    },
                },

                fa: {
                    ...fa,
                    messages: {
                        ...fa.messages,
                        phone: " {field}      ",
                        address: " {field}     ",
                    },
                },

                fr: {
                    ...fr,
                    messages: {
                        ...fr.messages,
                        phone: "Ce {field} doit tre un numro de tlphone valide",
                        address: "Cette {field} doit tre une adresse valide",
                    },
                },

                he: {
                    ...he,
                    messages: {
                        ...he.messages,
                        phone: " {field}     ",
                        address: " {field}    ",
                    },
                },

                hi_IN: {
                    ...hi_IN,
                    messages: {
                        ...hi_IN.messages,
                        phone: " {field}      ",
                        address: " {field}     ",
                    },
                },

                it: {
                    ...it,
                    messages: {
                        ...it.messages,
                        phone: "Questo {field} deve essere un numero di telefono valido",
                        address: "Questo {field} deve essere un indirizzo valido",
                    },
                },

                ja: {
                    ...ja,
                    messages: {
                        ...ja.messages,
                        phone: "{field}",
                        address: "{field}",
                    },
                },

                nl: {
                    ...nl,
                    messages: {
                        ...nl.messages,
                        phone: "Dit {field} moet een geldig telefoonnummer zijn",
                        address: "Dit {field} moet een geldig adres zijn",
                    },
                },

                pl: {
                    ...pl,
                    messages: {
                        ...pl.messages,
                        phone: "To {field} musi by prawidowy numer telefonu",
                        address: "To {field} musi by prawidowym adresem",
                    },
                },

                pt_BR: {
                    ...pt_BR,
                    messages: {
                        ...pt_BR.messages,
                        phone: "Este {field} deve ser um nmero de telefone vlido",
                        address: "Este {field} deve ser um endereo vlido",
                    },
                },

                ru: {
                    ...ru,
                    messages: {
                        ...ru.messages,
                        phone: " {field}     ",
                        address: " {field}    ",
                    },
                },

                sin: {
                    ...sin,
                    messages: {
                        ...sin.messages,
                        phone: " {field}      ",
                        address: " {field}     ",
                    },
                },

                tr: {
                    ...tr,
                    messages: {
                        ...tr.messages,
                        phone: "Bu {field} geerli bir telefon numaras olmaldr",
                        address: "Bu {field} geerli bir adres olmaldr",
                    },
                },

                uk: {
                    ...uk,
                    messages: {
                        ...uk.messages,
                        phone: " {field}     ",
                        address: " {field}    ",
                    },
                },

                zh_CN: {
                    ...zh_CN,
                    messages: {
                        ...zh_CN.messages,
                        phone: " {field} ",
                        address: " {field} ",
                    },
                },
            }),

            validateOnBlur: true,
            validateOnInput: true,
            validateOnChange: true,
        });
    },
};
